<template>
    <div>
        <loader :is-visible="isLoading"></loader>
        <div :class="ReportListParameters == undefined ? 'right-content hr_jobpost w-100' : 'right-content'">
            <div class="col-md-12 p-0 mb-2">
                <div class="theme-primary partition p-actions-expand">
                    <span class="p-name text-white">{{ reportName }}</span>
                    <span class="p-actions float-right mydbsize">
                        <a v-on:click="OpenFilter"
                            :class="isFilterExist > 0 ? 'p-action-btn text-white dash' : 'p-action-btn text-white dash'">
                            <em class="fa fa-filter"></em><br>{{ $t('Filter') }}
                        </a>
                        <a v-on:click="EditReport"
                            :class="CanEdit == true ? 'p-action-btn text-white dash' : 'p-action-btn text-white dash d-none'"
                            title="Edit Report">
                            <em class="fa fa-pencil text-white action-icon"></em><br>{{ $t('Edit') }}</a>
                        <a v-on:click="ExportToPDF('download')" class="p-action-btn text-white dash"
                            data-original-title="ExportToPDF">
                            <em class="fa fa-file-pdf-o"></em><br>{{ $t('PDF') }}</a>
                        <a v-on:click="ExportToPDF('print')" class="p-action-btn text-white dash"
                            data-original-title="Print">
                            <em class="fa fa-file-pdf-o"></em><br>{{ $t('PRINT') }}</a>
                        <a v-on:click="ExportToExcel" class="p-action-btn text-white dash"
                            data-original-title="ExportToExcel">
                            <em class="fa fa-file-excel-o"></em><br>{{ $t('Excel') }}</a>
                        <a v-show="showGraphBtn" v-on:click="OpenGraph" class="p-action-btn text-white dash">
                            <em class="fa fa-pie-chart"></em><br>{{ showgraph ? $t('CollapseGraph') : $t('ExpandGraph') }}
                        </a>
                        <a v-on:click="OpenSortingPopup" class="p-action-btn text-white dash" data-original-title="Sort">
                            <em class="fa fa-sort-amount-asc"></em><br>{{ $t('Sort') }}
                        </a>
                        <a v-on:click="OnBack" class="p-action-btn text-white dash" data-original-title="Back">
                            <em class="fa fa-angle-double-left"></em><br>{{ $t('Back') }}
                        </a>
                    </span>
                    <span class="collapse-head-btns">
                        <a href="javascript:;" class="collapse-btns">
                            <em class="fa fa-angle-up text-white"></em>
                        </a>
                    </span>
                </div>

                <div class="parentdiv datarepfix2">
                    <div class="verticle-action lft75 rt30">
                        <span class="action-list-box small" style="width:400px">
                            <span id="action-list1"
                                class="text-left p-0 datarep mt-3 w-90 float-left bg_outerdark borderbababa position-relative"
                                v-if="isSortPopup">
                                <div class="float-left col-12">
                                    <div class="container-fluid p-0">
                                    </div>
                                    <div class="row px-3 pt-3 pb-0" v-for="(input, index) in sortColummnarray">
                                        <div class="col-lg-5 pl-0 pr-3">
                                            <div class="form-group mb-0">
                                                <label class="pb-2">{{ $t('SortBy') }}</label>
                                                <select v-model="input.sortcolummnId" class="form-control"
                                                    :id="'SortValue' + index">
                                                    <option
                                                        v-for="item in reportListingInfo.FIELDS.filter(x => x.Calculated_Field == false)"
                                                        :value="item.CustomFieldId != '' ? item.CustomFieldId : item.FieldName"
                                                        :key="item.CustomFieldId"
                                                        :disabled="(sortColummnarray.filter(x => x.sortcolummnId == item.CustomFieldId)).length > 0">
                                                        {{ item.DisplayName ? GetLocalizedValue(item.DisplayName) :
                                                            GetLocalizedValue(item.FieldName) }}</option>
                                                </select>
                                            </div>
                                        </div>
                                        <div class="col-lg-5 pl-0 pr-3">
                                            <div class="form-group mb-0">
                                                <label class="pb-2">{{ $t('OrderBy') }}</label>
                                                <select v-model="input.columnOrders" class="form-control">
                                                    <option>{{ $t('ASC') }}</option>
                                                    <option>{{ $t('DESC') }}</option>
                                                </select>
                                            </div>
                                        </div>
                                        <div class="col-lg-2 pl-0 pr-3 d-flex align-items-center">
                                            <div class="form-group mb-0">
                                                <a v-on:click="AddSortingForm(input)"><em
                                                        class="fa fa-plus-circle font-24 text-success mt-3"></em></a>
                                                <a v-show="sortColummnarray[0].sortcolummnId != ''"
                                                    v-on:click="RemoveSortingForm(input)"><em
                                                        class="fa fa-times-circle font-24 text-danger mt-3 ml-1"></em></a>
                                            </div>
                                        </div>
                                    </div>
                                    <a class="btn btn-success my-3 mr-2 pl-1" v-on:click="EnableSortingSearch(input)">
                                        <span class="fa fa-sort-amount-desc mr-2"></span>{{ $t('Sort') }}</a>
                                    <a class="btn btn-danger pl-1 mb-0" v-on:click="RemoveSortingFromList()"> <span
                                            class="fa fa-times mr-2"></span>{{$t('ResetSort')}}</a>
                                </div>
                                <div class="va_lft25 top4"><em class="icon-solid arrow-solid-left text-secondary"
                                        style="font-size: 20pt;"></em>
                                </div>
                                <button type="button" class="close closebtnsmall" v-on:click="isSortPopup = false">
                                    <em aria-hidden="true" class="fa fa-times text"></em>
                                </button>
                            </span>
                        </span>
                    </div>
                </div>
            </div>
            <div class="col-md-12 p-0 mb-2">
                <div class="tab-pane mb-0">
                    <div class="tab-content fsmtabcontrolMobileView" id="myTabContent">
                        <!-- Graph  -->
                        <div v-if="showgraph" class="card mb-3 probx">
                            <div class="card-header">
                                <h2>{{ $t('Graph') }}</h2>
                            </div>
                            <div class="card-body" id="Reportpdf">
                                <div class="row">
                                    <div class="col-md-12 col-lg-12 text-center">
                                        <div class="border bg-light position-absolute text-left p-0"
                                            style="z-index:99; height:auto; max-width:20%;">
                                            <ul class="graphinfo">
                                                <li><label>X-axis:</label> {{ GetLocalizedValue(XAxisFieldName) }}</li>
                                                <li><label>Y-axis:</label> {{ GetLocalizedValue(YAxisFieldName) }}</li>
                                                <li v-show="aggregateFunctionResult != 0">
                                                    <label># {{ AggregateFuncField }}:</label>{{ aggregateFunctionResult }}
                                                </li>

                                            </ul>
                                        </div>
                                        <div class="vue-apexcharts"
                                            v-if="chartType == 'line' || chartType == 'bar' || chartType == 'horizontal_bar'">
                                            <VueApexCharts ref="chart" :type="chartType" height="auto" width="40%"
                                                :options="chartOptions" :series="chartSeries">
                                            </VueApexCharts>
                                        </div>
                                        <div class="vue-apexcharts" v-if="chartType == 'pie' || chartType == 'donut'">
                                            <VueApexCharts ref="chart" :type="chartType" height="auto" width="40%"
                                                :options="chartOptions" :series="chartSeries">
                                            </VueApexCharts>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <!-- Graph  -->

                        <!-- Listing Table -->
                        <div class="tab-pane p-0 pb-2 border-0 active" role="tabpanel" id="tab-detail-content"
                            aria-labelledby="tab-detail-content">
                            <div class="listing">
                                <div class="table-responsive table-fix-header">
                                    <table class="table table-bordered mt-0" style="width:100%" v-if="noRecord"
                                        aria-label="NO Record Found">
                                        <thead>
                                            <th scope="col"></th>
                                        </thead>
                                        <tbody>
                                            <tr style="cursor:initial;">
                                                <td colspan="4">
                                                    <div
                                                        class="text-center text-danger d-flex align-items-center justify-content-center norecs">
                                                        {{ $t("NoRecordfound") }}</div>
                                                </td>
                                            </tr>
                                        </tbody>
                                    </table>

                                    <table class="table table-bordered mt-0" style="width:100%" id="Reportpdf"
                                        aria-label="View Record">

                                        <thead class="thead-bg">
                                            <tr>
                                                <th scope="col" v-for="(item, pindex) in previewgroupdata" :key="pindex"
                                                    v-if="showdata">
                                                    {{ GetLocalizedValue((item.DisplayName != null && item.DisplayName !=
                                                        '') ?
                                                        item.DisplayName : item.FieldName) }}
                                                </th>
                                                <th scope="col" v-for="(column, cindex) in previewcolumn" :key="cindex">
                                                    <span v-on:click="sortdata(column.FieldName, column.CustomFieldId)"
                                                        v-if="!column.Calculated_Field" v-bind:class="{
                                                            'sort': true, 'tb_headerSortUp': SortExp == 'ASC' && SortBy == column.FieldName,
                                                            'tb_headerSortDown': SortExp == 'DESC' && SortBy == column.FieldName
                                                        }">
                                                        {{ GetLocalizedValue((column.DisplayName != null &&
                                                            column.DisplayName
                                                            != '') ? column.DisplayName : column.FieldName) }}
                                                    </span>
                                                    <span v-else>
                                                        {{ GetLocalizedValue((column.DisplayName != null &&
                                                            column.DisplayName
                                                            != '') ? column.DisplayName : column.FieldName) }}
                                                    </span>
                                                </th>
                                            </tr>
                                        </thead>

                                        <tbody v-for="(item, previewindex) in previewdata" :key="previewindex">
                                            <tr v-for="(sitem, sindex) in previewdata[previewindex].data" v-if="showdata"
                                                :key="sindex">
                                                <template v-if="sindex == 0">
                                                    <td v-for="(gitem, gindex) in previewgroupdata" :key="gindex"
                                                        :class="{ 'onlyrtbod': true, '': item.totalRecord == null }"
                                                        :rowspan="item.totalRecord">{{ (gitem.DisplayName != null &&
                                                            gitem.DisplayName != '') ? item[gitem.DisplayName] :
                                                            item[gitem.FieldName] }} ({{ item.totalRecord }})</td>
                                                </template>
                                                <td v-for="(column, groupindex) in previewcolumn" :key="groupindex">
                                                    <span
                                                        v-html="(sitem[column.DisplayName] != null && sitem[column.DisplayName] != '') ?
                                                            (sitem[column.DisplayName] || sitem[column.DisplayName] == 0 ? sitem[column.DisplayName] : '') :
                                                            (sitem[column.FieldName] || sitem[column.FieldName] == 0 ? sitem[column.FieldName] : '')">
                                                    </span>
                                                </td>
                                            </tr>
                                            <tr v-if="showcoldata">
                                                <td v-for="(column, columnindex) in previewcolumn" :key="columnindex">
                                                    <span
                                                        v-html="(item[column.DisplayName] != null && item[column.DisplayName] != '') ?
                                                            (item[column.DisplayName] || item[column.DisplayName] == 0 ? item[column.DisplayName] : '') :
                                                            (item[column.FieldName] || item[column.FieldName] == 0 ? item[column.FieldName] : '')">
                                                    </span>
                                                </td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                                <Pager :class="{ 'd none': true, '': TotalRecords > 10 }" :numberofrecords="TotalRecords"
                                    @SetCurrentPage="SetCurrentPage" prev-text="Prev" first-text="First" last-text="Last"
                                    next-text="Next">
                                </Pager>
                            </div>
                        </div>
                        <!-- Listing Table -->
                    </div>
                    <!-- <div
                        :class="TotalRecords > 100000 ? 'table-responsive table-fix-header' : 'table-responsive table-fix-header d-none'">
                        <table class="table table-bordered mt-0" style="width:100%" aria-label="NO Record Found">
                            <thead>
                                <th scope="col"></th>
                            </thead>
                            <tbody>
                                <tr style="cursor:initial;">
                                    <td colspan="4">
                                        <div
                                            class="text-center text-danger d-flex align-items-center justify-content-center norecs">
                                            <a @click="DownloadExceedRecords"
                                                class="text-center text-danger d-flex align-items-center justify-content-center norecs">
                                                {{ $t("LimitExceed") }}</a>
                                        </div>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div> -->
                </div>
            </div>
        </div>
        <modalViewCustomReportFilter v-if="showViewCustomReportFilter" @close="showViewCustomReportFilter = false"
            :reportListingInfo="reportListingInfo">
        </modalViewCustomReportFilter>
    </div>
</template>
<script>
import ViewReportServices from "@/services/DataService";
import modalViewCustomReportFilter from "../../../components/Common/Modal/modalViewCustomReportFilter.vue"
import { storeToRefs } from "pinia";
import { useloggedUserInfo } from "@/stores/UserInfo";
const { GetUserInfo } = storeToRefs(useloggedUserInfo());
import VueApexCharts from "vue-apexcharts";
import Pager from '../../../components/Common/Pager.vue';
import * as pdfFonts from "pdfmake/build/vfs_fonts.js"; // <-- vfs_fonts has to be imported before pdfmake
import pdfMake from 'pdfmake';
pdfMake.vfs = pdfFonts.pdfMake.vfs;


export default {
    components: {
        VueApexCharts,
        Pager,
        modalViewCustomReportFilter
    },
    data() {
        return {
            // filter
            showViewCustomReportFilter: false,
            isFilterExist: 0,
            isLoading: false,
            showdata: false,
            showcoldata: true,
            showgraph: false,
            reportName: '',
            showGraphBtn: true,
            viewreportparameters: '',
            Viewcount: 0,

            //Sorting
            isSortPopup: false,
            sortColummnarray: [{ sortcolummnId: "", columnOrders: "" }],
            SortExp: null,
            SortBy: null,

            //Chart 
            chartTypes: null,
            chartType: "",
            chartOptions: {},
            chartSeries: [],
            chartOptionXAxisData: [],
            chartSeriesData: [],
            colourList: ['#8892d0', '#da9c49', '#94c5a3', '#e67d21', '#787878', '#c59e71', '#38b8df', '#f55e1a', '#207a92', '#13599c'],
            AggregateFuncField: null,
            aggregateFunctionResult: 0,
            XAxisFieldName: null,
            YAxisFieldName: null,
            groupGraphlist: [],
            GraphJson: [],

            //List
            previewcolumn: [],
            previewgroupdata: [],
            reportListingInfo: [],
            previewdata: [],
            CanEdit: true,

            //pagination
            TotalRecords: 0,
            PageNumber: 1,
            PageSize: 10,
            TotalPages: 0,

            //Export to PDF
            allReocrdsData: null,
            TableBody: [],
            TableHeader: [],
            pdfpreviewdata: [],
            ExcelJsonData: null,
            count: 0,
            IsForPreview: 0,
            SingleSortArray: [],
            MainSortingArray: [],
            MainUpdatedArray: [],
            SelectedChartTypeId: '',
            noRecord: true
        }
    },
    props: {
        ReportListParameters: {
            type: Object
        }
    },
    created: async function () {
        var vm = this;
        vm.isLoading = true;
        vm.viewreportparameters = vm.ReportListParameters == undefined ? JSON.parse(atob(vm.$route.params.ReportID)) : vm.ReportListParameters;
        await vm.GetChartTypes();
        await vm.GetPreviewData(vm.viewreportparameters.ReportID);
        if (vm.viewreportparameters.ACCESS == 1) {
            vm.CanEdit = false;
        }
        else {
            vm.CanEdit = true;
        }
        vm.Viewcount += 1;
    },
    methods: {
        SetCurrentPage(e) {
            var vm = this;
            if (e.PagerMethod == 'a') {
                vm.pagerMethod(e.PageNumber);
            }
            else if (e.PagerMethod == 'b') {
                vm.SetPageSize(parseInt(e.PageSize));
            }

        },
        // open filter
        OpenFilter: function () {
            var vm = this;
            vm.showViewCustomReportFilter = true;
            $("#modalViewCustomReportFilter").modal("show");
            setTimeout(function () {
                $("#modalViewCustomReportFilter").modal('show');
                setTimeout(function () {
                    vm.SetBootstraptoolTip();
                }, 200);
            }, 1000);
        },
        RemoveNewFilterFromList() {
            var vm = this;
            vm.isLoading = true;
            vm.GetPreviewData(vm.viewreportparameters.ReportID);
            vm.MainUpdatedArray = [];
            vm.noRecord = false;
        },
        // show graph model based on ExpandGraph or CollapseGraph click
        OpenGraph() {
            var vm = this;
            if (vm.showgraph) {
                vm.showgraph = false;
            }
            else {
                vm.showgraph = true;
            }
        },

        //  on Click back button 
        OnBack: function () {
            var vm = this;
            vm.$router.push({ name: 'CustomReports' })
        },

        // View/fetch  data on click of View button and based on its reportId
        GetPreviewData(ReportId) {
            var vm = this;
            var params = `ReportId=${ReportId}&IsPaged=1`
            ViewReportServices.GetReportDetailsbyId(params).then(async (response) => {
                if (response.data == "No Data") {
                    vm.ShowAlert(vm.$t("ReportNotExist"), 'failure', true, vm.$t('Alert'));
                    vm.$router.push({ name: 'CustomReports' });
                    vm.isLoading = false;
                } else {
                    if (response.data != null && response.data != undefined && response.data != "") {
                        vm.reportListingInfo = response.data.JSON_To_Create_ReportListing[0].Info;
                        vm.isFilterExist = vm.reportListingInfo.FILTERS.length;
                        var listingData = response.data.ReportListing;
                        vm.reportName = vm.reportListingInfo.REPORTNAME;
                        vm.previewcolumn = [];
                        if (listingData != null && listingData != undefined && listingData == 1) {
                            if (vm.reportListingInfo.GRAPH.length > 0) {
                                await vm.SetGroupDataforGraph();
                            }
                            await vm.UpdatePreviewData();
                            await vm.GetSortingData();
                            vm.BindGraph();
                        }
                        else {
                            vm.noRecord = true;
                            vm.showgraph = false;
                            vm.showGraphBtn = false;
                            vm.isLoading = false;
                        }
                    } else {
                        vm.noRecord = true;
                        vm.showgraph = false;
                        vm.showGraphBtn = false;
                        vm.isLoading = false;
                    }
                }
            })
        },

        // Bind the Column List without grouping 
        Bindlisting: function (listingData) {
            var vm = this;
            if (listingData != null && listingData != undefined) {
                for (let j in vm.reportListingInfo.FIELDS) {
                    if (vm.reportListingInfo.FIELDS[j].FieldName && vm.reportListingInfo.FIELDS[j].DisplayName != 'ROWNUMBER'
                        && vm.reportListingInfo.FIELDS[j].FieldName != 'Total_Records' && vm.reportListingInfo.FIELDS[j].DisplayName != 'Total_Records') {
                        vm.previewcolumn.push({
                            "FieldName": vm.reportListingInfo.FIELDS[j].DisplayName != null && vm.reportListingInfo.FIELDS[j].DisplayName != '' ? vm.reportListingInfo.FIELDS[j].DisplayName :
                                vm.reportListingInfo.FIELDS[j].FieldName,
                            "CustomFieldId": vm.reportListingInfo.FIELDS[j].CustomFieldId, "Calculated_Field": vm.reportListingInfo.FIELDS[j].Calculated_Field
                        })
                    }
                }
                vm.noRecord = false;
            }
            else {
                vm.noRecord = true;
            }
        },

        // Bind the column list if the list is grouped.
        BindGroupListing: function (listingData) {
            var vm = this;
            vm.previewgroupdata = [];
            vm.showdata = true;
            vm.showcoldata = false;
            if (listingData != null && listingData != undefined) {
                for (let i in vm.reportListingInfo.GROUP) {
                    vm.previewgroupdata.push({
                        "FieldName": vm.reportListingInfo.GROUP[i].DisplayName != null && vm.reportListingInfo.GROUP[i].DisplayName != '' ? vm.reportListingInfo.GROUP[i].DisplayName :
                            vm.reportListingInfo.GROUP[i].FieldName,
                        "CustomFieldId": vm.reportListingInfo.FIELDS[i].CustomFieldId
                    })
                    for (let j in vm.reportListingInfo.FIELDS) {
                        if (vm.reportListingInfo.GROUP[i].FieldName != vm.reportListingInfo.FIELDS[j].FieldName) {
                            vm.previewcolumn.push({
                                "FieldName": vm.reportListingInfo.FIELDS[j].DisplayName != null && vm.reportListingInfo.FIELDS[j].DisplayName != '' ? vm.reportListingInfo.FIELDS[j].DisplayName :
                                    vm.reportListingInfo.FIELDS[j].FieldName,
                                "CustomFieldId": vm.reportListingInfo.FIELDS[j].CustomFieldId, "Calculated_Field": vm.reportListingInfo.FIELDS[j].Calculated_Field
                            })
                        }
                    }
                }
                vm.noRecord = false;
            }
            else {
                vm.noRecord = true;
            }
        },

        // fetch the data for Graph
        async SetGroupDataforGraph(sortColummnarray, UpdatedFilterArray) {
            var vm = this;
            vm.IsFilterValid();
            var temp = {
                "Info": {
                    "REPORTID": vm.reportListingInfo.REPORTID,
                    "REPORTNAME": vm.reportListingInfo.REPORTNAME,
                    "MODULENAME": vm.reportListingInfo.MODULENAME,
                    "SUBMODULECODE": vm.reportListingInfo.SUBMODULECODE,
                    "SUBMODULEID": vm.reportListingInfo.SUBMODULEID,
                    "PAGESIZE": parseInt(vm.PageSize),
                    "PAGENUMBER": parseInt(vm.PageNumber),
                    "IS_PAGED": 0,
                    "IsForPreview": vm.IsForPreview,
                    "FIELDS": vm.reportListingInfo.FIELDS,
                    "FILTERS": UpdatedFilterArray == undefined ? vm.reportListingInfo.FILTERS : UpdatedFilterArray,
                    "GROUP": [],
                    "CHARTGROUPARRAY": vm.reportListingInfo.CHARTGROUPARRAY,
                    "SORTING": vm.reportListingInfo.SORTING,
                    "GRAPH": vm.reportListingInfo.GRAPH,
                    "EXPRESSION": vm.reportListingInfo.EXPRESSION,
                    "EXPRESSIONFIELDSARRAY": vm.reportListingInfo.EXPRESSIONFIELDSARRAY
                }

            }
            var jsondata = JSON.stringify(temp);
            await ViewReportServices.PreviewReport(jsondata).then((response) => {
                if (response.data != null && response.data != undefined) {
                    vm.groupGraphlist = JSON.parse(response.data[0].Value);
                    if (UpdatedFilterArray != undefined) {
                        vm.BindGraph();
                    }
                }
            }).catch((err) => {
                console.log(err);
            })
        },

        // fetch the data when user change the paging 
        UpdatePreviewData(sortColummnarray, UpdatedFilterArray) {
            var vm = this;
            vm.isLoading = true;
            var sortingArray = [];
            vm.IsFilterValid();
            vm.MainSortingArray = sortColummnarray;
            vm.MainUpdatedArray = UpdatedFilterArray;
            for (var i in sortColummnarray) {
                if (sortColummnarray[i].sortcolummnId != undefined && sortColummnarray[i].sortcolummnId != "") {
                    sortingArray.push(
                        { "CustomFieldId": sortColummnarray[i].sortcolummnId, "SORTTYPE": sortColummnarray[i].columnOrders, "SortIndex": parseInt(i), "Calculated_Field": "" }
                    )
                    vm.reportListingInfo.FIELDS.forEach(arritem => {
                        var elem = sortingArray.find(x => x.CustomFieldId === arritem.CustomFieldId)
                        if (elem != undefined) {
                            elem.Calculated_Field = arritem.Calculated_Field;
                        }
                    })
                }
            }
            if (vm.SingleSortArray.length > 0 && sortingArray.length > 0) {
                const findsamesortvalue = sortingArray.findIndex(x => x.CustomFieldId === vm.SingleSortArray[0].sortcolummnId);
                if (findsamesortvalue != -1) {
                    sortingArray.splice(findsamesortvalue, 1);
                    if (vm.sortColummnarray.length > 1) {
                        vm.sortColummnarray.splice(findsamesortvalue, 1);
                    } else {
                        vm.sortColummnarray[findsamesortvalue].sortcolummnId = '';
                        vm.sortColummnarray[findsamesortvalue].columnOrders = '';
                    }
                }
            }
            var temp = {
                "Info": {
                    "REPORTID": vm.reportListingInfo.REPORTID,
                    "REPORTNAME": vm.reportListingInfo.REPORTNAME,
                    "MODULENAME": vm.reportListingInfo.MODULENAME,
                    "SUBMODULECODE": vm.reportListingInfo.SUBMODULECODE,
                    "SUBMODULEID": vm.reportListingInfo.SUBMODULEID,
                    "PAGESIZE": vm.PageSize,
                    "PAGENUMBER": parseInt(vm.PageNumber),
                    "IS_PAGED": 1,
                    "IsForPreview": vm.IsForPreview,
                    "FIELDS": vm.reportListingInfo.FIELDS,
                    "FILTERS": UpdatedFilterArray == undefined ? vm.reportListingInfo.FILTERS : UpdatedFilterArray,
                    "GROUP": vm.reportListingInfo.GROUP,
                    "SORTING": sortingArray == null || sortingArray == [] || sortingArray.length == 0 ? vm.reportListingInfo.SORTING : sortingArray,
                    "EXPRESSION": vm.reportListingInfo.EXPRESSION,
                    "EXPRESSIONFIELDSARRAY": vm.reportListingInfo.EXPRESSIONFIELDSARRAY,
                    "SINGLE_SORT": vm.SingleSortArray
                }
            }
            var jsondata = JSON.stringify(temp);
            ViewReportServices.PreviewReport(jsondata).then(async (response) => {
                if (response.data != null && response.data != undefined) {
                    vm.previewcolumn = [];
                    var listingInfo = JSON.parse(response.data[0].Value);
                    if (listingInfo != null && listingInfo != undefined) {
                        var MetaData = listingInfo.MetaData[0];
                        vm.previewdata = listingInfo.ListingData;
                        vm.TotalRecords = MetaData.TotalRecord;
                        vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                        if (MetaData.IsGrouping) {
                            vm.BindGroupListing(vm.previewdata);
                        }
                        else {
                            vm.Bindlisting(vm.previewdata);
                        }
                    }
                    else {
                        vm.noRecord = true;
                        vm.TotalRecords = 0;
                        vm.showgraph = false;
                        vm.showGraphBtn = false;
                    }
                    vm.isLoading = false;
                }
                else {
                    vm.isLoading = false;
                }
            }).catch((err) => {
                console.log(err);
            })

        },

        // Bind the graph Values
        BindGraph: function () {
            var vm = this;
            let graphData = vm.reportListingInfo.GRAPH[0]
            if (graphData != null && graphData != undefined && graphData != '') {
                vm.chartType = vm.chartTypes.find(x => x.id == graphData.chartTypeID).chart_Type.toLowerCase();
                vm.SelectedChartTypeId = graphData.chartTypeID;
                vm.AggregateFuncField = graphData.AggregateFunction;
                vm.XAxisFieldName = vm.FilterColumnlist(graphData.XAxisField).CustomFieldName;
                vm.YAxisFieldName = vm.FilterColumnlist(graphData.YAxisField).CustomFieldName;
                if (vm.groupGraphlist.MetaData[0].IsGrouping) {
                    vm.SetGroupingGraphData(graphData.AggregateFunction);
                }
                vm.SetGraphValues(vm.GraphJson, vm.XAxisFieldName, vm.YAxisFieldName);
                vm.CalculationAggregateFunctionResult(graphData.AggregateFunction);
                vm.DrawGraph(graphData);
                vm.showgraph = true;
            }
            else {
                vm.showgraph = false;
                vm.showGraphBtn = false;
            }
        },
        SetGroupingGraphData(FuntionType) {
            var vm = this;
            let ListingData = vm.groupGraphlist.ListingData;
            let setGraphArray = [];
            let XaxisField = vm.XAxisFieldName;
            let yAxisField = vm.YAxisFieldName;
            if (ListingData != null && ListingData != undefined) {
                ListingData.forEach((item, mainindex) => {
                    if (item[XaxisField] != undefined || XaxisField != undefined) {
                        if (FuntionType == "Count" || FuntionType == "Sum" || FuntionType == "Avg" || FuntionType == "Min" || FuntionType == "Max") {
                            var pushitem = item[XaxisField] != undefined ? item[XaxisField] : item.data[0][XaxisField] + ` (${item.totalRecord})`;
                            for (var i = 0; i <= parseInt(item.totalRecord); i++) {
                                if (i == 0) {
                                    setGraphArray.push({
                                        [XaxisField]: pushitem,
                                        [yAxisField]: item.GData != null && item.GData != 0 && item.GData > item.totalRecord ? item.GData : item.totalRecord
                                    });
                                }
                            }
                        }
                    }
                })
                vm.GraphJson = setGraphArray
                vm.showgraph = true;
            } else {
                vm.GraphJson = [];
                vm.XAxisFieldName = null;
                vm.YAxisFieldName = null;
                vm.showgraph = false;
                vm.showGraphBtn = false;
            }
        },
        // Draw graph 
        DrawGraph: function (graphData) {
            var vm = this;
            var isHorizontal = false;
            if (vm.chartType == "bar" || vm.chartType == "line" || vm.chartType == 'horizontal_bar') {
                if (vm.chartType == 'horizontal_bar') {
                    vm.chartType = "bar";
                    isHorizontal = true;
                }
                vm.chartSeries = [{
                    name: vm.$t(vm.FilterColumnlist(graphData.YAxisField).CustomFieldName),
                    data: vm.chartSeriesData
                }]
                vm.chartOptions = {
                    chart: {
                        toolbar: {
                            export: {
                                svg: {
                                    filename: vm.reportName,
                                },
                                png: {
                                    filename: vm.reportName,
                                },
                                csv: {
                                    headerCategory: vm.$t(vm.FilterColumnlist(graphData.XAxisField).CustomFieldName),
                                    filename: vm.reportName,
                                }
                            }
                        }
                    },
                    xaxis: {
                        categories: vm.chartOptionXAxisData,
                        title: {
                            text: isHorizontal ? vm.$t(vm.FilterColumnlist(graphData.YAxisField).CustomFieldName) : vm.$t(vm.FilterColumnlist(graphData.XAxisField).CustomFieldName),
                        },
                        formatter: function (val) {
                            return isHorizontal ? val : val != undefined ? vm.TruncateStr(val,6) : val;
                        }
                    },
                    yaxis: {
                        title: {
                            text: isHorizontal ? vm.$t(vm.FilterColumnlist(graphData.XAxisField).CustomFieldName) : vm.$t(vm.FilterColumnlist(graphData.YAxisField).CustomFieldName)
                        },
                        labels: {
                            formatter: function (val) {
                                return val.toLocaleString("en-US");
                            }
                        }
                    },
                    plotOptions: isHorizontal ? { bar: { borderRadius: 4, horizontal: true, } } : {},
                    dataLabels: {
                        enabled: true,
                    },
                    zoom: {
                        enabled: true,
                    }
                }
            }
            if (vm.chartType == "pie" || vm.chartType == "donut") {
                vm.chartSeries = vm.chartSeriesData;
                vm.chartOptions = {
                    labels: vm.chartOptionXAxisData,
                    colors: vm.colourList,
                }
            }
        },
        TruncateStr: function (str, len) {
            var convertstring = '' + str;
            if (convertstring != null)
                if (convertstring.length <= len) return str;
                else return convertstring.substring(0, len) + "...";
        },

        // Set the Chart Xaxis and Yaxis
        SetGraphValues(data, XFieldName, YFieldName) {
            var vm = this;
            vm.chartOptionXAxisData = [];
            vm.chartSeriesData = [];
            data.forEach(items => {
                if (XFieldName in items) {
                    if (items[XFieldName] != null && items[XFieldName] != undefined) {
                        vm.chartOptionXAxisData.push(items[XFieldName])
                        //set graph Values if XField  is Not Null  
                        vm.BindXYFields(items, XFieldName, YFieldName);

                    }
                    else {
                        //set graph Values if XField  is  Null
                        if (items[YFieldName] != null && items[YFieldName] != undefined) {
                            if (typeof XFieldName == "string") {
                                vm.chartOptionXAxisData = [...vm.chartOptionXAxisData, "-"]
                            }
                            else {
                                vm.chartOptionXAxisData = [...vm.chartOptionXAxisData, 0]
                            }
                            vm.BindXYFields(items, XFieldName, YFieldName);
                        }

                    }
                }
            });
        },
        //set graph X and Y fields 
        BindXYFields: function (items, XFieldName, YFieldName) {
            var vm = this;
            if (YFieldName in items) {
                if (items[YFieldName] != null && items[YFieldName] != undefined) {
                    if (typeof items[YFieldName] == "string") {
                        var replaceXCurrencySymbol = items[YFieldName].replace(/[$]/g, '')
                        vm.chartSeriesData.push(parseInt(replaceXCurrencySymbol))
                    }
                    else {
                        vm.chartSeriesData.push(items[YFieldName])
                    }
                }
                else {
                    vm.chartSeriesData = [...vm.chartSeriesData, 0]
                }
            }
        },

        // Filter column List baesd on CustomFieldId
        FilterColumnlist(CustomFieldId) {
            var vm = this;
            let Columnlist = vm.reportListingInfo.FIELDS
            let CustomFieldName, ColumnFieldType;
            for (let [index] of Columnlist.entries()) {
                if (Columnlist[index].CustomFieldId == CustomFieldId) {
                    if (Columnlist[index].DisplayName != null && Columnlist[index].DisplayName != '') {
                        CustomFieldName = Columnlist[index].DisplayName;
                    }
                    else {
                        CustomFieldName = Columnlist[index].FieldName;
                    }
                    ColumnFieldType = Columnlist[index].ColumnFieldDataCode;
                }
            }
            return { 'CustomFieldName': CustomFieldName, 'ColumnFieldType': ColumnFieldType }
        },

        // Calculating  Aggregate Function Result
        CalculationAggregateFunctionResult: function (functionType) {
            var vm = this;
            var tempval = 0;
            if (functionType == "Sum") {
                var sum = 0;
                vm.chartSeriesData.forEach(item => {
                    sum += item;
                });
                vm.aggregateFunctionResult = sum.toFixed(2);
                vm.aggregateFunctionResult = parseFloat(vm.aggregateFunctionResult).toLocaleString('en-US');
            }
            if (functionType == "Count") {
                var aggregatecount = 0;
                aggregatecount = vm.chartSeriesData.length;
                vm.aggregateFunctionResult = aggregatecount;
            }
            if (functionType == "Avg") {
                var total = 0;
                var count = 0;
                vm.chartSeriesData.forEach(function (item, index) {
                    total += item;
                    count++;
                });
                tempval = total / count;
                vm.aggregateFunctionResult = tempval.toFixed(2);
                vm.aggregateFunctionResult = parseFloat(vm.aggregateFunctionResult).toLocaleString('en-US');
            }
            if (functionType == "Min") {
                tempval = Math.min.apply(Math, vm.chartSeriesData.filter(Number));
                vm.aggregateFunctionResult = tempval.toFixed(2);
                vm.aggregateFunctionResult = parseFloat(vm.aggregateFunctionResult).toLocaleString('en-US');
            }
            if (functionType == "Max") {
                tempval = Math.max(...vm.chartSeriesData)
                vm.aggregateFunctionResult = tempval.toFixed(2);
                vm.aggregateFunctionResult = parseFloat(vm.aggregateFunctionResult).toLocaleString('en-US');
            }
        },

