            if (vm.reportListingInfo.SORTING != null && vm.reportListingInfo.SORTING != undefined && vm.reportListingInfo.SORTING != '' && vm.reportListingInfo.SORTING.length > 0) {
                vm.sortColummnarray = [];
                vm.reportListingInfo.SORTING.forEach((item, index) => {
                    vm.sortColummnarray.push({
                        "sortcolummnId": item.CustomFieldId,
                        "columnOrders": item.SORTTYPE
                    })
                })
            }
            else {
                vm.sortColummnarray = [{ sortcolummnId: "", columnOrders: "" }]
            }
        },
        GetTotalRecordsForExceed() {
            var CustomTotalRecords = 100000;
            return CustomTotalRecords;
        },
        GetPdfAndExcelApi() {
            var vm = this;
            vm.isLoading = true;
            vm.IsFilterValid();
            var exceljson = {
                "Info": {
                    "REPORTID": vm.reportListingInfo.REPORTID,
                    "REPORTNAME": vm.reportListingInfo.REPORTNAME,
                    "MODULENAME": vm.reportListingInfo.MODULENAME,
                    "SUBMODULECODE": vm.reportListingInfo.SUBMODULECODE,
                    "SUBMODULEID": vm.reportListingInfo.SUBMODULEID,
                    "PAGESIZE": 10,
                    "PAGENUMBER": 1,
                    "IS_PAGED": 0,
                    "IsForPreview": vm.IsForPreview,
                    "FIELDS": vm.reportListingInfo.FIELDS,
                    "FILTERS": vm.MainUpdatedArray != undefined ? vm.MainUpdatedArray : vm.reportListingInfo.FILTERS,
                    "GROUP": vm.reportListingInfo.GROUP,
                    "SORTING": vm.MainSortingArray != undefined ? vm.MainSortingArray : vm.reportListingInfo.SORTING,
                    "EXPRESSION": vm.reportListingInfo.EXPRESSION,
                    "EXPRESSIONFIELDSARRAY": vm.reportListingInfo.EXPRESSIONFIELDSARRAY,
                    "Req_From": "FromExcel"
                }
            }
            var EmailParam = JSON.stringify(exceljson);
            ViewReportServices.InsertDynamicReportsLog(EmailParam).then(response => {
                if (response.data[0].Status == 'Success') {
                    vm.ShowAlert(vm.$t("Email_Sent"), 'success', true, vm.$t('Alert'));
                    // ViewReportServices.GenerateReportEmailFromSchedule().then(res=>{
                    //     debugger
                    // }).catch(err=>{console.log(err.message)})
                    vm.isLoading = false;
                } else {
                    vm.ShowAlert(vm.$t("EmailFailed"), 'danger', true, vm.$t('Alert'));
                    vm.isLoading = false;
                }
            }).catch(err => {
                console.log(err.message);
            })
        },
        sortdata: function (filedName, fieldId) {
            var vm = this;
            var SortDir = "sort tb_headerSortDown";
            vm.SortBy = filedName;
            if (filedName === vm.SortBy) {
                if (vm.SortExp == "" || vm.SortExp == null) {
                    vm.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (vm.SortExp == "ASC") {
                    vm.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    vm.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            // var objSort = vm.sortColummnarray.filter(x=>x.sortcolummnId == fieldId)[0];
            // if(objSort !=undefined && objSort.columnOrders.length > 0){
            //     objSort.columnOrders=vm.SortExp;
            // }
            // else{
            //     vm.sortColummnarray.push({'sortcolummnId':fieldId,'columnOrders':vm.SortExp})
            // }
            // vm.UpdatePreviewData(vm.sortColummnarray);
            vm.SingleSortArray = [];
            vm.SingleSortArray.push({ 'sortcolummnId': fieldId, 'columnOrders': vm.SortExp })
            vm.UpdatePreviewData();
            $("#" + filedName + '  span:first-child').attr("class", SortDir);
        },
        IsFilterValid: function () {
            var vm = this;
            vm.reportListingInfo.FILTERS.forEach(fitem => {
                if (fitem.isManuallyAdded) {
                    if (fitem.operator_display_name == 'Is Empty' || fitem.operator_display_name == 'Is Not Empty' || fitem.operator_display_name == 'Is Off Day' || fitem.operator_display_name == 'Is Week End') {
                        console.log('Valid Filter')
                    }else if(fitem.FieldName != '' && fitem.Value1 != '' && fitem.OPERATOR_ID != '') {
                        console.log('Valid Filter')
                    }else {
                        let FilterNotValid = vm.reportListingInfo.FILTERS.findIndex(x => x.CustomFieldId === fitem.CustomFieldId);
                        vm.reportListingInfo.FILTERS.splice(FilterNotValid, 1);
                    }
                }
            })
        }
    }
}
</script>
